import React, { Fragment } from 'react';
import { Link } from 'gatsby';

import SEO from '../components/SEO';
import Projects from '../components/Projects';
import CTA from '../components/CTA';

const IndexPage = () => {
  return (
    <Fragment>
      <SEO title="Home" />
      <section className="contain mt-16 mb-32 md:my-32">
        <h1 className="text-3xl md:text-5xl">
          Safdar Jamal &mdash; Full-Stack Software Engineer.
        </h1>
        <p className="text-mdgray my-8 md:text-lg max-w-2xl">
          Full-stack software engineer and open-source developer, specializing
          in React, GraphQL, Node.js, and the extended JavaScript ecosystem.
        </p>
        <div>
          <Link className="link md:text-lg" to="/about">
            Learn more about me <span className="arrow">-&gt;</span>
          </Link>
        </div>
      </section>
      <section className="contain">
        <h2 className="text-2xl md:text-4xl mb-8">Featured Work</h2>
        <Projects />
        <div className="mt-8">
          <Link className="link md:text-lg" to="/portfolio">
            See more of my work <span className="arrow">-&gt;</span>
          </Link>
        </div>
      </section>
      <section className="contain mt-32">
        <CTA
          title="Ready to get started?"
          description="Letâ€™s talk about your project and how I can help you."
          buttonText="Get In Touch"
        />
      </section>
    </Fragment>
  );
};

export default IndexPage;
